<style>

#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 0px;
    height: 100vh;
}

.cloud {

    height: 75vh;
    z-index: -1;
}

.selector {
  z-index: 999;
}

</style>

<template>

<div id="app">
    <v-app> <!-- This is necessary for vuetify to behave properly. Don't forget it like the past two times you used vuetify -->
        <v-container fluid>
            <v-layout row wrap align-center justify-center >
                <v-flex xs12>
                  <h1>YouTube Channel Word Cloud</h1> <!-- Title, could be larger -->
                </v-flex>
                <v-flex xs5 class="selector">
                    <v-text-field label="Paste channel link here" v-model="url" v-on:keyup.enter="getCloud()"></v-text-field>
                </v-flex>
                <v-flex xs2 class="selector">
                    <v-select :items="items" label="No of videos (most recent)" v-model="selected"></v-select>
                </v-flex>
                <v-flex xs1 class="selector">
                    <v-btn color="teal" v-on:click="getCloud()">Go</v-btn>
                </v-flex>
                <v-flex xs12>
                    <div class="cloud">
                        <!-- Generating the word cloud. Consider making it scale with the content rather than being hard coded -->
                        <vue-word-cloud :words='words' :color="([, weight]) =>
                                       weight > 50 ? color50
                                      :weight > 20 ? color20
                                      :weight > 10 ? color10
                                      :weight > 5 ? color5
                                      :weight > 2 ? color1
                                      : '#34495e'"
                                      font-family="Impact"
                                      spacing="0.3">
                        </vue-word-cloud>
                    </div>
                </v-flex>
            </v-layout>

        </v-container>
        <div class="error">
            {{error}}
        </div>

    </v-app>
</div>

</template>

<script>

import ChannelService from './ChannelService';

export default {
    name: 'app',
    component: {},
    data() {
        return {
            words: [
                ["", 2]
            ],
            error: "",
            url: "",
            items: [
                "50",
                "100",
                "150",
                "200"
            ],
            selected: "50",
            color1: "#34495e",
            color2: "#9b59b6",
            color5: "#3498db",
            color10: "#2ecc71",
            color20: "#e8680d",
            color50: "#7f000e"
        }
    },
    methods: {
        getCloud: async function() {
            window.history.pushState('', '', '' + '/' + '');
            const idIndex = this.url.indexOf('/channel/') + 9; // Internal use only, the index of the channel string within the url string
            const urlLength = this.url.length; // total length of the url string
            const channelID = this.url.substring(idIndex, urlLength); // isolating and storing the channel string

            window.history.pushState('', '', channelID + '/' + this.selected); // push the link in the address bar
            try {
                this.words = await ChannelService.getWords(this.url, this.selected); // grab the words array from the backend
            } catch (err) {
                this.error = err;
            }
        }
    },
    mounted(){
      // On page load, if there is a path to a channel in the link, load the data
      let path =  window.location.pathname.split('/'); // grab the path from the address bar link, get the data in an array
      if(path[2]){
        // test if there is anything after the link; if there is, set the data in front end and execute the getCloud method
        this.url = 'https://www.youtube.com/channel/' + path[1];
        this.selected = path[2];
        this.getCloud();
      }
    }
}

</script>
